    font-size: 24px; } }

@media (max-width: 630px) {
  .browse {
    margin: 10px auto;
    width: 73px;
    font-size: 16px; } }

.images {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px; }

.pic {
  border: 1px solid #cccccc;
  box-shadow: 0px 0px 3px 1px  #cccccc;
  margin: 0px 0px 30px 35px;
  position: relative; }

img.picture {
  padding: 5px; }

.date {
  position: absolute;
  top: 175px;
  left: 10px;
  font-size: 19px;
  color: #fff; }

.text {
  display: flex;
  justify-content: space-around;
  position: relative;
  left: 3px;
  margin: 0px 5px; }

hr {
  margin: -10px 5px 5px 5px;
  opacity: 0.4; }

.social-network {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: -10px;
  position: relative;
  left: 26px;
  margin: 0px 28px; }

.socset {
  position: relative;
  display: inline-block;
  background: #999999;
  width: 20px;
  height: 20px;
  border-radius: 50%; }

.socset:hover {
  background: #283F95; }

.socset:hover::after {
  content: '';
  position: absolute;
  /* Абсолютное позиционирование */
  width: 0;
  height: 0;
  border: 10px solid;
  border-color: transparent transparent #000000 transparent;
  bottom: -50%;
  left: 50%;
  margin-left: -10px; }

.socset:hover::before {
  content: attr(data-title);
  position: absolute;
  /* Абсолютное позиционирование */
  left: -80%;
  top: 135%;
  /* Положение подсказки */
  z-index: 1;
  /* Отображаем подсказку поверх других элементов */
  background: #000000;
  /*  цвет фона */
  color: #fff;
  font-family: Arial, sans-serif;
  /* Гарнитура шрифта */
  font-size: 12px;
  /* Размер текста подсказки */
  text-align: center;
  width: 120px;
  padding: 2px 5px;
  /* Поля */
  border: 1px solid #333;
  /* Параметры рамки */
  border-radius: 7%; }

.center-img {
  display: block;
  margin: 3px auto; }

@media screen and (max-width: 990px) {
  .images {
    display: flex;
    justify-content: center;
    margin-right: 10px; } }

.footer {
  max-width: 1000px;
